<template>
  <div class="note">
    <!--div class="mark"><span class="glyphicon glyphicon-ok" aria-hidden="true"></span></div-->
    <div class="note-heading">
        <div class="note-title"><span>{{note.title}}</span></div>
        <div class="note-ctrl">
            <div class="note-edit"><span class="glyphicon glyphicon-pencil" aria-hidden="true"></span></div>
            <div class="note-save" @click="deleteNote"><span class="glyphicon glyphicon-trash" aria-hidden="true"></span></div>
        </div>
    </div>
    <div class="note-body">
        <p>{{note.body}}</p>
    </div>
  </div>
</template>



<script >
import store from "../store/index"
export default{
    props:["note"],
    methods:{
        deleteNote:function(){
            store.dispatch("deleteNote",this.$props.note);
        }
    }
}
</script>
<style scoped>
.note{
    width: 7rem;
    height: 10rem;
    font-size: 0.4rem;
    color:rgb(51,51,51);
    border-radius: 5px;
    box-shadow:0 0 0.3rem #CCC;
    margin:0.4rem;
    padding:0.1rem;
}

.note:hover{
    box-shadow:0 0 0.3rem #757575;
}
.note-title{
    width:3.5rem;
    font-size: 0.3rem;
    display: inline-block;
}
.note-title span{
    max-width: 3.5rem;
    display: inline-block;
    border-bottom: 1px solid #CCC;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.note-ctrl{
    display: inline-block;
}
.note-edit,.note-save{
    width: 1rem;
    cursor: pointer;
    display: inline-block;
    text-align: center;
    position: relative;
    left:0.3rem;
}
.note-body{
    max-height: 9rem;
    text-indent: 1em;
    line-height: 2em;
    word-wrap: break-word;
    word-break: normal;
    overflow-y: scroll;
    text-overflow: ellipsis;
}
</style>



