<template>
    <div>
        <headTop></headTop>
        <div class="flex-container">
          <Note v-for="note in notes" :note="note" key="note.ID"></Note>
        </div>
        <FootGuide></FootGuide>
    </div>
</template>

<script>
import headTop from "../components/header"
import FootGuide from "../components/footer";
import Note from "../components/note";
import {mapGetters} from "vuex";
import store from "../store/index";

export default{
    components:{
        headTop,
        Note,
        FootGuide,
    },
    computed:{
      notes(){
        console.log(store.state.reloadNotesFlag);
        return store.state.notes;
      }
    }

}
</script>
<style>
.flex-container{
  display: flex;
  display: -webkit-flex;
  flex-wrap:wrap;
  justify-content:center;
}
.flex-item{
}
.head-ctrl{
  width: 3.5rem;
  height: 1.5rem;
  line-height: 1.5rem;
  display: inline-block;
  font-size: 0.6rem;
  color: #FFF;
  text-align: center;
  cursor:pointer;
}
.router-link-active{
  color: #020202;
}

</style>



