import Vuex from "vuex";
import Vue from "vue";
import actions from "./actions";
import mutations from "./mutations";


const state ={
    reloadNotesFlag: 0,
    notes:{
        2:{
            ID:2,
            title:"明朝有哪些有趣的冷知识？",
            body: "明朝有一帮热衷当神仙的文人。他们先是从古代的道经中翻出了一个预言「龙沙谶」，算出万历年间将有「八百地仙」飞昇出世，遂坚信自己有份，并四处搜罗预兆，甚至把丰臣秀吉都扯了进来。在当时的江南文人圈里，简直是开谈不说龙沙谶，读尽诗书也枉然。",
        },
        1:{
            ID:1,
            title:"如何看待台湾要将蒋介石铜像沉入水底？",
            body: "对大陆来说，蒋就是一饭桶。4亿老百姓给他出钱出粮出人，结果东北不抵抗、华北被扫荡、炸花园口、烧长沙，连国都都被屠了。",
        },
    },
}
Vue.use(Vuex);

export default new Vuex.Store({
    state,
    actions,
    mutations,
})



