/*
notes:{
    234123413:{  //ID:是addNote时的时间秒
        ID:234123413,
        otherData
    }
}
*/

export default {
    addNote(state, payload){
        console.log("action addNote");
        state.notes[payload.ID] = payload;
        state.reloadNotesFlag ++;
    },
    changeNote(state, payload){
        console.log("action changeNote");
        Object.assign(state.notes, payload);//把生成时的时间当作对象的key，assign合并二个对象,相同属性时会set属性

    },
    deleteNote(state, payload){
        console.log("action deleteNote");

        delete state.notes[payload.ID + ""];
        state.reloadNotesFlag ++;
    },
}



