<template>
  <div class="flex-container booter">
    <router-link  tag="div"  :to="index.path"  class=" head-ctrl">
      <span class="glyphicon glyphicon-home" aria-hidden="true"> {{index.title}}</span>
    </router-link>
    <router-link  tag="div"  :to="newNote.path"  class=" head-ctrl">
      <span class="glyphicon glyphicon-plus" aria-hidden="true"> {{newNote.title}}</span>
    </router-link>
    <router-link  tag="div"  :to="search.path"  class=" head-ctrl">
      <span class="glyphicon glyphicon-search" aria-hidden="true"> {{search.title}}</span>
    </router-link>
  </div>
</template>



<script >
export default{
  data(){
    return {
        newNote: {path:"/newNote", title: "新建"},
        search:  {path:"/search",  title: "查找"},
        index:   {path:"/index",   title: "主页"}
    }
  }
}
</script>
<style >

.booter{
  position: fixed;
  bottom: 0;
  width:100%;
  height: 1.5rem;
  background-color: #0665c4;
  justify-content:space-around;
}

</style>




